<template>
  <div class="pa-2">
    <!-- this is PatientsList Page with table -->
    <h1>Patients List</h1>
    <v-row justify="end" class="pb-5">      
      <v-col cols="12" md="6" lg="4">
        <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
      </v-col>
    </v-row>

    <v-data-table
      :headers="patientHeaders"
      :items="patients"
      :items-per-page="5"
      :expanded.sync="expanded"
      show-expand
      :search="search"
    >      
      <template v-slot:item.actions="{ item }">
        <v-btn 
        icon 
        x-small 
        class="mr-2" 
        :to="{name: 'newPatient', params: {editUser: {...item}}}"
        title="Click to edit this patient"
        > 
          <v-icon>mdi-pencil</v-icon> 
        </v-btn>
        <v-btn 
        icon 
        x-small
        @click="delPatient(item)" 
        title="Click to delete this patient">
          <v-icon>mdi-delete</v-icon>
        </v-btn>
      </template>
      <template v-slot:item.created_by="{ value }">
        {{ value.last_name }} {{ value.first_name }}
      </template>
      <template v-slot:item.modified_by="{ value }">
        {{ value.last_name }} {{ value.first_name }}
      </template>
      <template v-slot:item.created_on="{ value }">
        {{ formatDate(value) }}
      </template>
      <template v-slot:item.modified_on="{ value }">
        {{ formatDate(value) }}
      </template>
      <template v-slot:expanded-item="{ item }">
        <v-container>  
          <!-- {{ item }}         -->
          <v-expansion-panels inset focusable multiple>
            <v-expansion-panel>
              <v-expansion-panel-header><b>Maternal Demographics</b></v-expansion-panel-header>
              <v-expansion-panel-content>
                  <li>
                    <b>Address:</b> {{item.mat_demo.address}}
                  </li>
                  <li>
                    <b>Contact Phone Number on case note:</b> {{item.mat_demo.phoneNo}} 
                  </li>
                  <li>
                    <b>Mother&apos;s Age:</b> {{item.mat_demo.age}}
                  </li>
                  <li>
                    <b>Mother&apos;s Parity:</b> {{item.mat_demo.parity}}
                  </li>
                  <li>
                    <b>Maternal level of education:</b> {{item.mat_demo.matEdu}}
                  </li>
                  <li>
                    <b>Mother&apos;s Occupation:</b> {{item.mat_demo.occupation}}
                  </li>
              </v-expansion-panel-content>
            </v-expansion-panel>
            <v-expansion-panel>
              <v-expansion-panel-header><b>Maternal Obstetric history and paternal history</b></v-expansion-panel-header>
              <v-expansion-panel-content>
                <li>
                  <b>Maternal Medical Conditions in Pregnancy:</b> {{item.mat_pat_hist.matPregCond}}
                </li>
                <li>
                  <b>Previous Pregnancy Loss:</b> {{item.mat_pat_hist.prevPregLoss}}
                </li>
                <li>
                  <b>Previous Neonatal Death:</b> {{item.mat_pat_hist.prevNeonatalDeath}}
                </li>
                <li>
                  <b>Maternal Illness on Admission:</b> {{item.mat_pat_hist.matIllness}}
                </li>
                <li>
                  <b>ANC:</b> {{item.mat_pat_hist.anc}}
                </li>
                <li v-if="item.mat_pat_hist.anc == 'Yes'">
                  <b>ANC upto Four Visits:</b> {{item.mat_pat_hist.ancFourVisits}}
                </li>
                <li v-if="item.mat_pat_hist.anc == 'Yes'">
                  <b>Place of ANC:</b> {{item.mat_pat_hist.ancPlace}}
                </li>
                <li>
                  <b>Place of Delivery:</b> {{item.mat_pat_hist.deliveryPlace}}
                </li>
                <li>
                  <b>Mode of Delivery:</b> {{item.mat_pat_hist.deliveryMode}}
                </li>
                <li v-if="item.mat_pat_hist.deliveryMode == 'ELCS' || item.mat_pat_hist.deliveryMode == 'EMCS'">
                  <b>C/S indication:</b> {{item.mat_pat_hist.csIndication}}
                </li>
                <li>
                  <b>Father&apos;s Age:</b> {{item.mat_pat_hist.patAge}}
                </li>
                <li>
                  <b>Father&apos;s Level of Education:</b> {{item.mat_pat_hist.patEdu}}
                </li>
                <li>
                  <b>Father&apos;s Occupation:</b> {{item.mat_pat_hist.patOccupation}}
                </li>
              </v-expansion-panel-content>
            </v-expansion-panel>
            <v-expansion-panel>
              <v-expansion-panel-header><b>Baby&apos;s Data</b></v-expansion-panel-header>
              <v-expansion-panel-content>
                <v-window v-model="window" >
                  <v-window-item :value="0">
                    <li>
                      <b>Date of Birth:</b> {{item.baby_data.dob}}
                    </li>
                    <li>
                      <b>Date of Admission:</b> {{item.baby_data.doa}}
                    </li>
                    <li>
                      <b>Sex:</b> {{item.baby_data.sex}}
                    </li>
                    <li>
                      <b>Gestational Age (weeks):</b> {{item.baby_data.gestationAge}}
                    </li>
                    <li>
                      <b>Age at Admission (hours/days if older than 72 hrs):</b> {{item.baby_data.AdmissionAge}} {{ item.baby_data.admissionAgeUnit }} 
                    </li>
                    <li>
                      <b>Time duration between onset of illness to presentation in facility:</b> {{item.baby_data.illnessDuration}} {{ item.baby_data.illnessDurationUnit }} 
                    </li>
                    <li>
                      <b>Birth Weight:</b> {{item.baby_data.birthWeight}} 
                    </li>
                    <li>
                      <b>Weight at Presentation:</b> {{item.baby_data.presentationWeight}} 
                    </li>
                  </v-window-item>
                  <v-window-item :value="1">
                    <li>
                      <b>OFC(cm):</b> {{item.baby_data.ofc}} 
                    </li>
                    <li>
                      <b>Length(cm):</b> {{item.baby_data.ofcLength}} 
                    </li>
                    <li>
                      <b>Temperature at Admission(<sup>o</sup>C):</b> {{item.baby_data.admissionTemp}} 
                    </li>
                    <li>
                      <b>Temperature at Admission(<sup>o</sup>C):</b> {{item.baby_data.admissionTemp}} 
                    </li>
                    <h5>Initial Diagnosis at Admission:</h5>
                    <li v-for="diag in item.baby_data.initialDiagnosis" :key="diag.id">
                      {{diag.diagnosis}} 
                    </li>
                    <h5>Other Morbidities Developed on Admission:</h5>
                    <li v-for="morb in item.baby_data.morbidities" :key="morb.id">
                      {{morb.morbidity}} 
                    </li>
                  </v-window-item>
                  <v-window-item :value="2">
                    <h3>Investigations</h3>
                    <li>
                      <b>PCV on Admission(%):</b> {{item.baby_data.pcvAdmission}} 
                    </li>
                    <li>
                      <b> RBS on Admission(mmoL/L):</b> {{item.baby_data.rbsAdmission}} 
                    </li>
                    <li v-if="item.baby_data.fbcResult == 'None' || !item.baby_data.fbcResult">
                      <b>FBC Result:</b> {{item.baby_data.fbcResult}} 
                    </li>
                    <div v-else>
                      <h5>FBC Result</h5>
                      <li>
                        <b>PCV(%):</b> {{item.baby_data.pcv}} 
                      </li>
                      <li>
                        <b>WBC Total(x10<sup>9</sup>/L):</b> {{item.baby_data.wbc}} 
                      </li>
                      <li>
                        <b>Neut(%):</b> {{item.baby_data.neut}} 
                      </li>
                      <li>
                        <b>Lymph(%):</b> {{item.baby_data.lymph}} 
                      </li>
                      <li>
                        <b>Baso(%):</b> {{item.baby_data.baso}} 
                      </li>
                      <li>
                        <b>Eosin(%):</b> {{item.baby_data.eosin}} 
                      </li>
                      <li>
                        <b>Platelet(%):</b> {{item.baby_data.platelet}} 
                      </li>
                      <li>
                        <b>MCV(%):</b> {{item.baby_data.mcv}} 
                      </li>
                      <li>
                        <b>MCHC(%):</b> {{item.baby_data.mchc}} 
                      </li>                      
                    </div>
                    <li>
                      <b>Blood Culture Result:</b> {{item.baby_data.bloodCulture}} 
                    </li>
                    
                    <h5>Other Investigations:</h5>
                    <v-data-table
                    :headers="investigationHeaders"
                    :items="item.baby_data.otherInvestigations"
                    ></v-data-table>
                  </v-window-item>
                  <v-window-item :value="3">
                    <li>
                      <b>Blood Transfusions:</b> {{item.baby_data.bloodTrans}} 
                    </li>
                    <li v-if="item.baby_data.bloodTrans == 'Yes'">
                      <b>Number of Blood Transfusions:</b> {{item.baby_data.bloodTransNo}} 
                    </li>
                    <li v-if="item.baby_data.bloodTrans == 'Yes'">
                      <b>Indication for Blood Transfusions:</b> {{item.baby_data.bloodTransIndication}} 
                    </li>
                    <li>
                      <b>Phototherapy during Admission:</b> {{item.baby_data.admissionPhototherapy}} 
                    </li>
                    <li>
                      <b>Surgery during Admission:</b> {{item.baby_data.admissionSurgery}} 
                    </li>
                    <h5>Specify Antibiotics during Admission and Duration:</h5>
                    <v-data-table
                    :headers="antibioticHeaders"
                    :items="item.baby_data.admissionAntibiotics"
                    ></v-data-table>
                  </v-window-item>
                  <v-window-item :value="4">
                    <li>
                      <b>Preterm:</b> {{item.baby_data.preterm}} 
                    </li>
                    <div v-if="item.baby_data.preterm == 'Yes'">
                      <li>
                        <b>At what postnatal age is birth weight regained (days)?</b> {{item.baby_data.weightRegainAge}} 
                      </li>
                      <li>
                        <b>At what postnatal age was oral feeds at 120mls/kg achieved(days)?</b> {{item.baby_data.oralFeedAge}} 
                      </li>
                      <li>
                        <b>At what postnatal age is birth weight regained (days)?</b> {{item.baby_data.weightRegainAge}} 
                      </li>
                      <li>
                        <b>Discharge:</b> {{item.baby_data.pretermDischarge}} 
                      </li>
                      <div v-if="item.baby_data.pretermDischarge == 'Yes'">
                        <li>
                          <b>Weight at discharge (Grams):</b> {{item.baby_data.dischargeWeight}} 
                        </li>
                        <li>
                          <b>Postnatal age at discharge (days):</b> {{item.baby_data.dischargePostnatalAge}} 
                        </li>
                        <li>
                          <b>PCA at discharge:</b> {{item.baby_data.dischargePCA}} 
                        </li>                        
                      </div>
                    </div>
                  </v-window-item>
                  <v-window-item :value="5">
                    <h5>Final Diagnosis:</h5>
                    <li v-for="diag in item.baby_data.finalDiagnosis" :key="diag.id">
                      {{diag.diagnosis}} 
                    </li>
                    <li>
                      <b>Duration of Admission till discharge/death (days):</b> {{item.baby_data.durationTillDischarge}} 
                    </li>
                    <li>
                      <b>Outcome of Illness:</b> {{item.baby_data.illnessOutcome}} 
                    </li>
                    <li>
                      <b>DAMA:</b> {{item.baby_data.dama}} 
                    </li>
                    <li v-if="item.baby_data.illnessOutcome == 'Discharge'">
                      <b>State Morbidities at Discharge:</b> {{item.baby_data.dischargeMorbidities}} 
                    </li>
                    <h5>State Possible Causes of Death:</h5>
                    <li v-for="cause in item.baby_data.deathCauses" :key="cause.id">
                      {{cause.cause}} 
                    </li> 
                  </v-window-item>
                </v-window>
                <v-row>
                  <v-btn
                    small
                    text
                    :disabled="window==0"
                    @click="window--"
                    color="success">
                    prev
                  </v-btn>
                  <v-spacer></v-spacer>
                  <v-btn
                    small
                    text
                    :disabled="window==5"
                    @click="window++"
                    color="success">
                    next
                  </v-btn>
                </v-row>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </v-container>
      </template>
    </v-data-table>
    <v-snackbar
      v-model="snackbar"
      :color="snackColor"
      bottom
      :timeout="5000"
    >
      {{ snackMsg }}

      <template v-slot:action="{ attrs }">
        <v-btn
          color="red"
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          <v-icon>mdi-close</v-icon>
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import {apiClient} from "@/services"
export default {
  name: 'PatientsList',

  data: () => ({
    window: 1,
    expanded: [],
    search: '',
    snackbar: false,
    snackMsg: '',
    snackColor: 'success',
    patientHeaders: [
      {
        text: "Patient's Name",
        align: 'start',
        sortable: true,
        filterable: true,
        value: 'name',
      },
      {
        text: "Hospital Number",
        sortable: true,
        filterable: true,
        value: 'hosp_no',
      },
      {
        text: "Created On",
        sortable: true,
        filterable: true,
        value: 'created_on',
      },
      {
        text: "Created By",
        sortable: true,
        filterable: true,
        value: 'created_by',
      },
      {
        text: "Modified On",
        sortable: true,
        filterable: true,
        value: 'modified_on',
      },
      {
        text: "Modified By",
        sortable: true,
        filterable: true,
        value: 'modified_by',
      },
      {
        text: 'Details',
        value: 'data-table-expand'
      },
      {
        text: '',
        value: 'actions'
      }
    ],

    patients: [],
    investigationHeaders: [
      {
        text: "Investigation",
        align: 'start',
        sortable: true,
        value: 'investigation',
      },
      {
        text: "Result",
        sortable: false,
        value: 'result',
      },
      {
        text: "Unit",
        sortable: false,
        value: 'unit',
      }
    ],
    antibioticHeaders: [
      {
        text: "Antibiotic",
        align: 'start',
        sortable: true,
        value: 'antibiotic',
      },
      {
        text: "Indication",
        sortable: false,
        value: 'indication',
      },
      {
        text: "Duration (Days)",
        sortable: false,
        value: 'duration',
      }
    ],
  }),
  created(){
      apiClient
      .get('/?u=2')
      .then(res => {
        // console.log(res.data)
        if (Array.isArray(res.data)){        
          this.patients = [...res.data]
        } 
      })
      .catch(err => console.error(err.message))
    },
    methods: {
      formatDate(dateValue) {
        const months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
        const dt = new Date(dateValue)
        // return `${dt.getDate()}-${dt.getMonth()}-${dt.getFullYear()}`
        return `${months[dt.getMonth()]} ${dt.getDate()}, ${dt.getFullYear()}`
      },
      delPatient(patient) {
      const ok = confirm(`Are sure you want to delete ${patient.name} with Hospital No: ${patient.hosp_no}?`);
      if (ok) {
        this.displayMessage(`Deleting ${patient.name}, please wait...`);
        apiClient
          .delete(`/?${patient.id}`)
          .then(res => {
            if (!res.data.ok){        
              this.displayMessage(`${patient.name} not successfully deleted`, 'error');
              return
            } 
            this.patients = this.patients.filter(pt => pt.id !== patient.id )
            this.displayMessage(`${patient.name} successfully deleted`)
          })
          .catch(err => this.displayMessage(`${err.message}`, 'error'));
      }
    },
    displayMessage(msg='Patient deleted successfully', type='success', state=true) {
      this.snackMsg = msg;
      this.snackColor = type;
      this.snackbar = state;
    },
  },
}
</script>
